ear button on edit mode */\n.edit-mode input,\n#new-task-date-reset {\n    border: 1px solid grey;\n}\n\n#new-task-name {\n    padding: 0.2em 0.3em;\n}\n\n#new-task-due-date {\n    padding: 0 0.5em;\n}\n\n#new-task-date-reset {\n    margin-left: 0.5em;\n    padding: 0 0.4em;\n}\n\n#new-task-date-reset:hover {\n    background: rgba(0, 0, 0, 0.5);\n    color: #fff;\n}\n\n/* container for flag icons */\n#new-task-priority-wrapper {\n    /* set height equal to container height instead of content height to fix \n    jumping effect that occurs when container height briefly changes upon \n    priority change */\n    align-self: stretch;\n    display: flex;\n    align-items: center;\n}\n\n/* hide default radio input */\ninput[type=\"radio\"] {\n    display: none;\n}\n\n/* when one of the radio input elements is selected, slightly increase the size\nof its corresponding flag */\ninput[type=\"radio\"]:checked + i {\n    font-size: 1.2em;\n}\n\n/* space out flags and smooth flag size change */\n#new-task-priority-wrapper i {\n    margin: 0 0.2em;\n    transition: font-size 0.2s;\n}\n\n/* container for both circle and check mark */\n.bullet-point {\n    position: relative;\n}\n\n/* circle-shaped span element */\n.check-circle {\n    border: 1px solid black;\n    height: 12px;\n    width: 12px;\n    border-radius: 50%;\n    display: inline-block;\n}\n\n/* check mark to indicate a task is completed */\n.check-mark {\n    font-size: 0.8em;\n    position: absolute;\n    left: 1px;\n    top: -1px;\n    opacity: 0;\n}\n\n.check-mark:hover {\n    opacity: 1;\n}\n\n.bullet-point:hover ~ .task-label {\n    text-decoration: line-through;\n    text-decoration-thickness: 0.2em;\n}\n\n.low-priority {\n    color: green;\n}\n\n.medium-priority {\n    color: orange;\n}\n\n.high-priority {\n    color: red;\n}\n\n.task-edit-btn {\n    color: rgb(35, 163, 248);\n}\n\n/*\n * Responsiveness\n */\n\n/* tablet display */\n@media (max-width: 1023px) {\n    main {\n        position: relative;\n    }\n 